// src/pages/ProductDetail.js
import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { addToCart } from '../features/cart/cartSlice';
import { useDispatch } from 'react-redux';
import { auth } from '../firebase'; // adjust if your export is in firebaseConfig.js
import Navbar from '../components/Navbar'; // optional if App already includes it

const ProductDetail = () => {
  const { id } = useParams();
  const [product, setProduct] = useState(null);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(true);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    setLoading(true);
    fetch(`https://fakestoreapi.com/products/${id}`)
      .then(res => {
        if (!res.ok) throw new Error('Failed to fetch product.');
        return res.json();
      })
      .then(data => {
        setProduct(data);
        setError('');
      })
      .catch(err => {
        setError(err.message || 'Something went wrong.');
      })
      .finally(() => setLoading(false));
  }, [id]);

  const handleAdd = () => {
    const user = auth.currentUser;
    if (!user) {
      // redirect to login and remember intent
      navigate('/login', { state: { intended: 'add-to-cart', productId: product?.id } });
      return;
    }
    if (product) {
      dispatch(
        addToCart({
          id: product.id,
          title: product.title,
          price: product.price,
          image: product.image,
          quantity: 1,
        })
      );
    }
  };

  if (loading)
    return (
      <div className="container my-5">
        <div className="text-center">Loading product...</div>
      </div>
    );

  if (error)
    return (
      <div className="container my-5">
        <div className="alert alert-danger">{error}</div>
      </div>
    );

  if (!product)
    return (
      <div className="container my-5">
        <div className="alert alert-warning">Product not found.</div>
      </div>
    );

  return (
    <div className="container my-5">
      {/* If Navbar isn't global you can include it; otherwise omit */}
      {/* <Navbar /> */}
      <div className="row g-4">
        <div className="col-md-6 text-center">
          <img
            src={product.image}
            alt={product.title}
            className="img-fluid"
            style={{ maxHeight: 400, objectFit: 'contain' }}
          />
        </div>
        <div className="col-md-6">
          <h2>{product.title}</h2>
          <p className="fw-bold h4">${product.price}</p>
          <p>{product.description}</p>
          <div className="d-flex gap-2">
            <button className="btn btn-primary" onClick={handleAdd}>
              Add to Cart
            </button>
            <button
              className="btn btn-outline-secondary"
              onClick={() => {
                // maybe go to cart or immediate buy flow
                handleAdd();
                navigate('/cart');
              }}
            >
              Buy Now
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductDetail;
