// pages/CartPage.js
import React, { useEffect, useState } from 'react';

const CartPage = () => {
  const [cartItems, setCartItems] = useState([]);

  useEffect(() => {
    const stored = localStorage.getItem('cart');
    setCartItems(stored ? JSON.parse(stored) : []);
  }, []);

  const getTotal = () => {
    return cartItems.reduce((sum, item) => sum + item.price, 0).toFixed(2);
  };

  const handleClearCart = () => {
    localStorage.removeItem('cart');
    setCartItems([]);
  };

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Your Cart</h1>
      {cartItems.length === 0 ? (
        <p>No items in cart.</p>
      ) : (
        <>
          <ul>
            {cartItems.map((item, index) => (
              <li key={index} className="mb-4 border-b pb-2">
                <img src={item.image} alt={item.title} className="h-20 inline-block mr-4" />
                <span>{item.title}</span> - ₹{item.price}
              </li>
            ))}
          </ul>
          <div className="mt-4 font-bold">Total: ₹{getTotal()}</div>
          <button
            className="mt-2 bg-red-600 text-white px-4 py-2 rounded hover:bg-red-700"
            onClick={handleClearCart}
          >
            Clear Cart
          </button>
        </>
      )}
    </div>
  );
};

export default CartPage;
